.section-projects{
    position: relative;
    margin-top: 1100px;
}

.project-title{
    width: 562px;
    font-size: 48px;
    font-weight: 400;
}

.projects-imgs{
    margin-top: 32px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px 20px;
}

.proj-img{
    width: 445px;
    height: 580px;
    background: #000;
    position: relative;
    
}

.proj-img:hover{
    .none{
        opacity: 1;
    }
}

.first-img{
    background: url("./../../img/projects/first-project.webp");
    position: relative;
}

.sec-img{
    background: url("./../../img/projects/second-project.webp");
}

.thir-img{
    background: url("./../../img/projects/third-project.webp");
}

.four-img{
    background: url("./../../img/projects/project-four.webp");
}

.five-img{
    background: url("./../../img/projects/five-project.webp");
}

.six-img{
    background: url("./../../img/projects/six-project.webp");
}

.none{
    opacity: 0;
    transition: opacity 0.3s;
}

.img-hover{
    width: 100%;
    background: rgba(33, 37, 41, 0.50);
    height: 12%;
    bottom: 0;
    position: absolute;
    color: #fff;
    font-family: $roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    justify-content: space-between;
    align-items: center;
    vertical-align: middle;
    display: flex;
    padding: 0 30px;

}

.arrow{
    vertical-align: middle;
    right: 0;
}

.img-text{
    vertical-align: middle;
    width: 100%;
}
