.section-work{
    margin-top: 500px;
    position: relative;
}

.work-title{
    font-size: 48px;
    font-weight: 400;
}

.work-right-side{
    right: 0;
    width: 678px;
    position: absolute;
    top: 0;
    right: 0;
    
}

.right-title{
    font-family: $roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    width: 325px;
    height: 50px;
}

.right-block-main{
    margin-top: 44px;
}

.block-title{
    width: 328px;
    height: 50px;
    flex-shrink: 0;
    font-size: 36px;
    font-weight: 400;
    top: 0;
    left: 0;
}

.block-desc{
    font-family: $roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    width: 329px;
    margin-left: 50%;
    top: 25px;
    position: absolute;

}

.right-block{
    width: 678px;
    height: calc(96px + 50px);
    position: relative;
    margin-top: 24px;
}

hr{
    width: 678px;
    height: 1px;
    background: #000;
}

