body{
    @include font-face('Helvetica Oblique','./../fonts/Helvetica-Oblique.woff');
    font-family: $helvetica;
}

.header{
    min-width: 100%;
    margin-top: 12px;

}

a{
    transition: color 0.3s ease-in;
    text-decoration: none;
    color: $color-main;
}



a:hover{
    color: $color-accent;
}

.nav__list{
    font-family: $roboto;
    font-weight: 400;
    min-width: 100%;
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between; /* для размещения элементов по краям контейнера */
    align-items: center; 
}

.nav__el{
    position: absolute;
    top: 50%;
}

.nav__podel{
    padding: 0;
    margin: 0;
}

.logo{
    top: 50px;
    left: 43px;
    width: 47px;
    height: 50px;
}

.nav__num{
    right: 5px;
    top: 0;
}



.nav__pod{
    right: 350px;
    margin: 0;
    padding: 0;
}

.main-header{
    margin-top: 160px;
}

.title{
    font-size: 72px;
    font-family: $helvetica;
    font-weight: 400;
    line-height: 120%;
    text-transform: uppercase;
    width: 794px;
    flex-shrink: 0;
}

.header__title{
    display: flex;
    flex-direction: row;
}

.header-order{
    position: absolute;
    right: 0;
    align-self: flex-end;
    margin-bottom: 20px;
}

.header__sub{
    margin-top: 40px;
    position: relative;
}

.sub_title{
    width: 445px;
    height: 84px;
    font-size: 20px;


}


.header-right-img{
    position: absolute;
    top: 0;
    right: 0;

}



.header-left-img{
    position: relative;
    bottom: 0;
    margin-top: 232px;
}


