.footer{
    margin-top: 120px;
    position: relative;
}

#popup-close{
    position: absolute;
    font-size: 20px;
    right: 0;
}



.footer-modal{
    top: 0;
    position: fixed;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 7;
    background-color: rgba(0,0,0,0.8);
}

.footer-cont{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
}

.left-img{
    left: 0;
}

.footer-right{
    right: 0;
    position: absolute;
    top: 0;
}

.fot-title{
    font-size: 48px;
    font-weight: 400;
}

.fot-desc{
    font-size: $roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    margin-top: 32px;
}

.footer-input{
    margin-top: 72px;
    &:focus{
        font-size: 20px;
    }
}

.footer-inp{
    margin-top: 16px;
}

.hr-inp{
    margin-top: 12px;
}

.footer-btn{
    margin-top: 30px;
    color: #fff;
    padding: 12px 20px;
    background: $color-main;
    transition: all 0.3s ease-in;
    &:hover{
        background: $color-accent;
        color: $color-main;
    }
    font-size: 16px;
    font-family: $roboto;
    font-weight: 400;
    line-height: 140%;
}

.socials{
    margin-top: 40px;
    gap: 8px;
}

.footer-down{
    margin-top: 120px;
    display: flex;
    bottom: 0;
    position: relative;
    justify-content: space-between;
    padding-bottom: 20px;
    height: 46px;
    align-items: flex-end;
}

.down-center{
    font-family: $roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
}

.down-side{
    font-family: $roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
}

.down-text{

    bottom: 0;
}